// WooCommerce Home Page Product Widget

.home-feature-3 .woocommerce ul.products li.product,
.home-feature-3 .woocommerce-page ul.products li.product {
	width: 29%;

	@include media($ipad-screen-up) {
		width: 48%;
	}

	@include media($small-screen-up) {
		width: 100%;
	}
}

@include media($xlarge-screen-up) {
	.home .wrap {
		width: auto;
	}
}
	// Home Feature BG

.home-feature-bg {
	overflow: hidden;
}

.home-feature-bg .wrap {
	background: #cde;
	margin: 0 auto;
	overflow: hidden;
	padding: 0;
	max-width: 1140px;
	text-align: center !important;
}

.home-feature-bg .featuredpage .page,
.home-feature-bg .featuredpost .post {
	float: left;
	margin: 0;
	overflow: hidden;
	padding: 0 0 5px;
}

.home-feature-bg .featuredpage .page h2,
.home-feature-bg .featuredpost .post h2 {
	margin: 10px 0;
}

.home-feature-bg .featuredpage .page h2 a,
.home-feature-bg .featuredpost .post h2 a {
	font-size: 15px;
}

.home-feature-bg .featuredpage img,
.home-feature-bg .featuredpost img {
	background: none;
	border: none;
	padding: 0;
}

.home-feature-bg .featuredpage img:hover,
.home-feature-bg .featuredpost img:hover {
	opacity: .7;
	transition: all .5s ease;
}

.home-feature-bg .byline,
.home-feature-bg .byline {
	font-size: 11px !important;
	line-height: 15px !important;
	margin: 0 0 8px !important;
}

.home-feature-bg p {
	margin: 0 0 25px;
}

/*!
Home Feature BG Alt
---------------------------------------------------------------------------------------------------- */

.home-feature-bg-alt {
	background: #cde;
	margin: 0 0 0 0;
}

.home-feature-bg-alt .alignleft { /*---PK---*/
	margin: 0 12px 0 0;
}

.home-feature-bg-alt .wrap {
	margin: 0 auto;
	overflow: hidden;
	padding: 0;
	//width: 1140px;
}

.home-feature-bg-alt .featuredpage .page,
.home-feature-bg-alt .featuredpost .post {
	float: left;
	margin: 0 1em 0 0;
	overflow: hidden;
	padding: 0 0 5px;
	width: 30%;
}

.home-feature-bg-alt .featuredpage img,
.home-feature-bg-alt .featuredpost img {
	background: none;
	border: none;
	padding: 0;
	border: 1px solid #fff;
	border-radius: 20px;
}

.home-feature-bg-alt .featuredpage img:hover,
.home-feature-bg-alt .featuredpost img:hover {
	opacity: .7;
	-moz-transition: all .5s ease;
	-o-transition: all .5s ease;
	-webkit-transition: all .5s ease;
	transition: all .5s ease;
}

.home-feature-bg-alt .byline,
.home-feature-bg-alt .byline {
	font-size: 11px !important;
	line-height: 15px !important;
	margin: 0 0 8px !important;
}

.home-feature-bg-alt p {
	font-size: 12px;
	margin: 0 0 0 0;
}

.home-feature-bg-alt .entry-header .entry-meta {
	border-bottom: 1px solid #ddd;
}

