// Float Clearing

.archive-pagination::before,
.clearfix::before,
.entry::before,
.entry-pagination::before,
.footer-widgets::before,
.nav-primary::before,
.nav-secondary::before,
.site-container::before,
.site-footer::before,
.site-header::before,
.site-inner::before,
.wrap::before {
	content: " ";
	display: table;
}

.archive-pagination::after,
.clearfix::after,
.entry::after,
.entry-pagination::after,
.footer-widgets::after,
.nav-primary::after,
.nav-secondary::after,
.site-container::after,
.site-footer::after,
.site-header::after,
.site-inner::after,
.wrap::after {
	clear: both;
	content: " ";
	display: table;
}

.clear {
	clear: both;
}
